pre code.hljs
    display: block
    overflow-x: auto
    padding: 1em
code.hljs
    padding: 3px 5px
.hljs
    background: #f3f3f3
    color: #444
    .hljs-keyword
        color: #5299D6
    .hljs-title
        color: #47C9B0
    .hljs-built_in
        color: $tc-blue
    .hljs-number
        color: #c77900
    .hljs-string
        color: #d37d0a
    .hljs-comment
        color: #697070
    .hljs-punctuation, .hljs-tag
        color: #444a
    .hljs-tag .hljs-attr, .hljs-tag .hljs-name
        color: #444
    .hljs-literal
        color: #695
    .hljs-meta
        color: #1f7199
    .hljs-meta .hljs-string
        color: #d37d0a
    .hljs-emphasis
        font-style: italic
    .hljs-strong
        font-weight: 700
    .hljs-addition, .hljs-built_in, .hljs-bullet, .hljs-code
        color: $tc-blue // #397300
    //.hljs-deletion, .hljs-number, .hljs-quote, .hljs-selector-class, .hljs-selector-id, .hljs-string, .hljs-template-tag, .hljs-type
    //    color: #800
    .hljs-doctag, .hljs-keyword, .hljs-literal, .hljs-name, .hljs-section, .hljs-selector-tag, .hljs-strong, .hljs-title, .hljs-type
        font-weight: 700
    .hljs-link, .hljs-operator, .hljs-regexp, .hljs-selector-attr, .hljs-selector-pseudo, .hljs-symbol, .hljs-template-variable, .hljs-variable
        color: #ab5656
    .hljs-section, .hljs-title
        //color: #800
        font-weight: 700
body.dark-mode
    .hljs
        color: #ddd
        background-color: $tc-black
    .hljs-keyword, .hljs-link, .hljs-literal, .hljs-section, .hljs-selector-tag
        color: #fff
    //.hljs-addition, .hljs-attribute, .hljs-built_in, .hljs-bullet, .hljs-name, .hljs-string, .hljs-symbol, .hljs-template-tag, .hljs-template-variable, .hljs-title, .hljs-type, .hljs-variable
    //    color: #d88
    .hljs-comment, .hljs-deletion, .hljs-meta, .hljs-quote
        color: #979797
    .hljs-doctag, .hljs-keyword, .hljs-literal, .hljs-name, .hljs-section, .hljs-selector-tag, .hljs-strong, .hljs-title, .hljs-type
        font-weight: 700
    .hljs-emphasis
        font-style: italic
